.service {
	width: 100%;
	padding-top: 55px;
	&__gallery {
		width: 100%;
		margin-top: 25px;
		padding-bottom: 100px;
	}
}
.slider {
	width: 100%;
	&__list {
		display: flex;
		justify-content: center;
		flex-wrap: wrap;
		width: 100%;
		max-height: 200px;
		overflow: hidden;
		@media(max-width: @phone) {
			max-height: 145px;
		}
	}
	&__item {
		margin-right: 10px;
	}
	&__item-img {
		width: 224px;
		height: 168px;
		@media(max-width: @phone) {
			width: 145px;
			height: 109px;
		}
	}
	&__item-text {
		margin-top: 8px;
		padding-bottom: 10px;
		text-align: center;
	}
	&__controls {
	display: flex;
	justify-content: center;
	width: 100%;
	margin-top: 24px;
		@media(max-width: @desktop) {
			display: none;
		}
	}
	&__btn-icon {
		width: 10px;
		height: 20px;
	}	
}
.slider-pagination {
	display: none;
	justify-content: center;
	width: 100%;
	margin-top: 25px;
	@media(max-width: @desktop) {
			display: flex;
	}
	&__item {
		display: block;
		width: 8px;
		height: 8px;
		margin-right: 5px;
		border-radius: 50%;
		border: 1px solid @c-main;
		outline: none;
		&:hover {
			cursor: pointer;
			transform: scale(1.2);
		}
		&:focus {
			transform: scale(1.2);
		}
		&--active {
			background-color: @c-main;
		}
	}
	
}
.block-links {
	display: flex;
	flex-wrap: wrap;
	width: 100%;
	margin-right: -10px;
	&__item {
		width: 32%;
		height: 215px;
		margin-top: 1%;
		margin-right: 1%;
		background:linear-gradient(0deg, rgba(85, 0, 106, 0.4), rgba(85, 0, 106, 0.4)), 
		url('@{bg-img}index-main-bg-desk.jpg') no-repeat center center,rgba(85, 0, 106, 0.7);
		background-size: cover;
	}
	&__item--big {
		width: 48.5%;
		height: 325px;
		margin-right: 1%;
		background-color: @c-blue;
	}
	&__link {
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
		padding: 40px;
		width: 100%;
		height: 100%;
	}
	&__text {
		position: relative;
		.ff-600();
		font-size: @font-24;
		text-align: center;
		color:@c-w;
	}
	&__text::after {
		content: '';
		position: absolute;
		bottom: -25px;
		left: 50%;
		transform: translateX(-50%); 
		width: 40px;
		height: 10px;
		background: url('@{bg-svg}arrow') no-repeat center center;
		background-size: 100%;
	}

}